<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Ujian</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-content class="ion-padding">
        <div v-if="!hasTakenExam">
          <div>
            <ion-item>
              <ion-label>Waktu Tersisa: {{ formatTime(timer) }}</ion-label>
            </ion-item>
          </div>
          <div v-if="currentQuestionIndex < questions.length">
            <ion-item>
              <ion-label>{{
                questions[currentQuestionIndex].question
              }}</ion-label>
            </ion-item>
            <ion-radio-group v-model="answers[currentQuestionIndex]">
              <ion-item>
                <ion-label>{{
                  questions[currentQuestionIndex].options.A
                }}</ion-label>
                <ion-radio slot="start" value="A"></ion-radio>
              </ion-item>
              <ion-item>
                <ion-label>{{
                  questions[currentQuestionIndex].options.B
                }}</ion-label>
                <ion-radio slot="start" value="B"></ion-radio>
              </ion-item>
              <ion-item>
                <ion-label>{{
                  questions[currentQuestionIndex].options.C
                }}</ion-label>
                <ion-radio slot="start" value="C"></ion-radio>
              </ion-item>
              <ion-item>
                <ion-label>{{
                  questions[currentQuestionIndex].options.D
                }}</ion-label>
                <ion-radio slot="start" value="D"></ion-radio>
              </ion-item>
              <ion-item>
                <ion-label>{{
                  questions[currentQuestionIndex].options.E
                }}</ion-label>
                <ion-radio slot="start" value="E"></ion-radio>
              </ion-item>
            </ion-radio-group>
            <ion-button @click="nextQuestion">
              {{
                currentQuestionIndex === questions.length - 1
                  ? "Finish"
                  : "Next"
              }}
            </ion-button>
          </div>
          <div v-else>
            <ion-item>
              <ion-label>Nilai Anda: {{ score }}</ion-label>
            </ion-item>
            <ion-button @click="submitExam">Submit Exam</ion-button>
          </div>
        </div>
        <div v-else>
          <ion-item>
            <ion-label
              >Anda sudah menyelesaikan ujian ini. Nilai Anda:
              {{ score }}</ion-label
            >
          </ion-item>
          <ion-button @click.prevent="toNavigate('lihathasilujianUser')"
            >Lihat Hasil Ujian</ion-button
          >
        </div>
      </ion-content>
      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>
<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonItem,
  IonLabel,
  IonRadioGroup,
  IonRadio,
  IonButton,
  IonToast,
  IonBackButton,
} from "@ionic/vue";
import { ref, onMounted, onUnmounted } from "vue";
import {
  getFirestore,
  doc,
  getDoc,
  collection,
  getDocs,
  setDoc,
  serverTimestamp,
} from "firebase/firestore";
import { useRoute, useRouter } from "vue-router";
import { getAuth } from "firebase/auth";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);
const auth = getAuth(firebaseApp);
const route = useRoute();
const router = useRouter();
const jadwalId = ref(route.params.jadwalId);
const questions = ref([]);
const currentQuestionIndex = ref(0);
const answers = ref([]);
const score = ref(0);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
const timer = ref(0);
const intervalId = ref(null);
const hasTakenExam = ref(false);

const fetchQuestions = async () => {
  const user = auth.currentUser;
  if (user) {
    const examResultDoc = await getDoc(
      doc(db, `examResults/${user.uid}/results`, jadwalId.value)
    );
    if (examResultDoc.exists()) {
      hasTakenExam.value = true;
      score.value = examResultDoc.data().score;
      return;
    }

    const jadwalDoc = await getDoc(doc(db, "jadwal", jadwalId.value));
    if (jadwalDoc.exists()) {
      const jadwalData = jadwalDoc.data();
      const questionsSnapshot = await getDocs(collection(db, "soal"));
      questions.value = questionsSnapshot.docs
        .map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }))
        .filter((question) => jadwalData.soal.includes(question.id));
      answers.value = new Array(questions.value.length).fill("");
      timer.value = jadwalData.durasi * 60; // Set timer in seconds
      startTimer();
    }
  }
};

const startTimer = () => {
  intervalId.value = setInterval(() => {
    if (timer.value > 0) {
      timer.value--;
    } else {
      clearInterval(intervalId.value);
      submitExam();
    }
  }, 1000);
};

const formatTime = (seconds) => {
  const minutes = Math.floor(seconds / 60);
  const secs = seconds % 60;
  return `${minutes.toString().padStart(2, "0")}:${secs
    .toString()
    .padStart(2, "0")}`;
};

const nextQuestion = () => {
  if (currentQuestionIndex.value < questions.value.length - 1) {
    currentQuestionIndex.value++;
  } else {
    calculateScore();
    currentQuestionIndex.value++;
  }
};

const calculateScore = () => {
  score.value = 0;
  questions.value.forEach((question, index) => {
    if (question.correctAnswer === answers.value[index]) {
      score.value++;
    }
  });
};

const submitExam = async () => {
  const user = auth.currentUser;
  if (user) {
    calculateScore();
    try {
      await setDoc(doc(db, `examResults/${user.uid}/results`, jadwalId.value), {
        userId: user.uid,
        jadwalId: jadwalId.value,
        score: score.value,
        createdAt: serverTimestamp(),
      });
      showToast("Ujian berhasil disubmit!", "success");
      hasTakenExam.value = true; // Set exam as taken
      router.push({ name: "siswa" });
    } catch (error) {
      console.error("Error submitting exam:", error);
      showToast("Gagal submit ujian.", "danger");
    }
  } else {
    showToast("Pengguna tidak terautentikasi.", "danger");
  }
};

const toNavigate = (routeName) => {
  router.push({ name: routeName });
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

onMounted(() => {
  fetchQuestions();
});

onUnmounted(() => {
  if (intervalId.value) {
    clearInterval(intervalId.value);
  }
});
</script>
